import axios from 'axios';
import authHeader from './auth-header';
// import { API_URL } from '../config';
const API_URL = "https://dolphin-app-lmvcw.ondigitalocean.app/api/";

// const API_URL = 'https://backgammoncloud-z3qxp.ondigitalocean.app/api/';
// const API_URL = 'https://backgammoncloud.com/api/';
// const API_URL = 'http://159.223.142.17:3001/api/';
// const API_URL = 'http://localhost:3001/api/';
// const API_URL = 'https://backgammongameproject.herokuapp.com/api/';
// const API_URL = 'http://localhost:8080/api/test/';

class UserService {
  getPublicContent() {
    return axios.get(API_URL + 'all');
  }

  getUserBoard() {
    return axios.get(API_URL + 'user', { headers: authHeader() });
  }

  getAdminBoard() {
    return axios.get(API_URL + 'admin', { headers: authHeader() });
  }

  getAllUsers() {
    return axios.get(API_URL + 'users', { headers: authHeader() });
  }

  deleteUser(deleteItem: string) {
    let data = {
      deleteItem: deleteItem
    }
    return axios.post(API_URL + 'delete', data, { headers: authHeader() });
  }
}

export default new UserService();